import React from 'react';
import styles from '../../styles/Landing/Landing.module.css'; 
import Profile from './Profile';
import Portfolio from './Portfolio';
import Skills from './Skills';
import Resume from './Resume';
import Contact from './Contact';
import background from '../../assets/images/landingBackground.jpg';

class Landing extends React.Component {
    state = {
        showPortfolio: false,
        showSkills: false,
        showResume: false,
        hoverPortfolio: false,
        hoverResume: false,
        hoverSkills: false,
    }

    onClickPortfolio = () => {
        this.setState({showPortfolio: true});
    }

    onClickSkills = () => {
        this.setState({showSkills: true});
    }

    onClickResume = () => {
        this.setState({showResume: true});
    }

    handleClickHome = (e) => {
        e.stopPropagation();
        this.setState({
            showPortfolio: false,
            showSkills: false,
            showResume: false,
        });
        this.setState({
            hoverPortfolio: true,
            hoverResume: false,
            hoverSkills: false,
        });
    }

    handleMouseEnter = (stateName) => {
        const stateObj = {};
        stateObj[stateName] = true;
        console.log(stateObj);
        this.setState(stateObj);
    }

    handleMouseLeave = (stateName) => {
        const stateObj = {};
        stateObj[stateName] = false;

        this.setState(stateObj);
    }

    render() {
        return (
            <div className={styles.page}>
                <div>
                    <img src={background} alt="" className={styles.background_img}/>
                </div>
                <div className={styles.all_divs}>
                    <div className={this.state.showPortfolio ? styles.portfolio_page : styles.portfolio_div} onClick={this.onClickPortfolio} onMouseEnter={() => this.handleMouseEnter("hoverPortfolio")} onMouseLeave={() => this.handleMouseLeave("hoverPortfolio")}>
                        <Portfolio showPortfolio={this.state.showPortfolio} handleClickHome={this.handleClickHome} hover={this.state.hoverPortfolio}/>
                    </div>
                    <div className={styles.second_row_divs}>
                        <div className={this.state.showSkills ? styles.skills_page : styles.skills_div} onClick={this.onClickSkills} onMouseEnter={() => this.handleMouseEnter("hoverSkills")} onMouseLeave={() => this.handleMouseLeave("hoverSkills")}>
                            <Skills showSkills={this.state.showSkills} handleClickHome={this.handleClickHome} hover={this.state.hoverSkills}/>
                        </div>
                        <div className={styles.second_row_right_divs}>
                            <div className={styles.profile_and_resume_div}>
                                <div className={styles.profile_div}>
                                    <Profile />
                                </div>
                                <div className={this.state.showResume ? styles.resume_page : styles.resume_div} onClick={this.onClickResume} onMouseEnter={() => this.handleMouseEnter("hoverResume")} onMouseLeave={() => this.handleMouseLeave("hoverResume")}>
                                    <Resume showResume={this.state.showResume} handleClickHome={this.handleClickHome} hover={this.state.hoverResume}/>
                                </div>
                            </div>
                            <div className={styles.contact_div}>
                                <Contact />
                            </div>
                        </div>
                        {/* <div className={styles.profile_and_contact_div}>
                            <div className={styles.profile_div}>
                                <Profile />
                            </div>
                            <div className={styles.contact_div}>
                                <Contact />
                            </div>
                        </div>
                        <div className={this.state.showResume ? styles.resume_page : styles.resume_div} onClick={this.onClickResume}>
                            <Resume showResume={this.state.showResume} handleClickHome={this.handleClickHome}/>
                        </div> */}
                    </div>
                </div>
            </div>
        );
    }
}

export default Landing;